@charset "UTF-8";
/*
 * OdenStyle
 * aigis-theme
 */
/*
---
name: ヘッダ
tag:
  - layouts
---

```html
<div class="aigis-sidemenu">ここにヘッダが入る</div>
```
*/
.aigis-sidemenu {
  max-width: 1200px;
  margin: 4em auto; }

/*
---
name: 本体
tag:
  - layouts
---

```html
<div class="aigis-contents">sample text</div>
```
*/
.aigis-contents {
  max-width: 1200px;
  margin: 1em auto; }

/*
---
name: フッタ
tag:
  - layouts
---

```html
<div class="aigis-footer">sample text</div>
```
*/
.aigis-footer {
  border-top: 1px solid #dde;
  text-align: center;
  margin-top: 4em;
  padding: 4em 0; }

body {
  padding: 0;
  margin: 0;
  font-size: 14px; }

/*
 * スタイルガイドのデフォルトのスタイル
 * (preview箇所以外に適応させる)
 */
/*
---
name: リンク
tag:
  - parts
---

<a href="#">リンク</a>
*/
.aigis-contents__heading,
.aigis-contents__body > :not(.aigis-module),
.aigis-module > :not(.aigis-preview),
.aigis-sidemenu,
.aigis-footer {
  font-family: '游ゴシック体', Yu Gothic, YuGothic, 'ヒラギノ角ゴシック Pro', Hiragino Kaku Gothic Pro, 'メイリオ', Meiryo, Osaka, 'ＭＳ Ｐゴシック', MS PGothic, sans-serifbody, serif; }
  .aigis-contents__heading a,
  .aigis-contents__body > :not(.aigis-module) a,
  .aigis-module > :not(.aigis-preview) a,
  .aigis-sidemenu a,
  .aigis-footer a {
    color: #08c;
    text-decoration: none; }
    .aigis-contents__heading a:hover,
    .aigis-contents__body > :not(.aigis-module) a:hover,
    .aigis-module > :not(.aigis-preview) a:hover,
    .aigis-sidemenu a:hover,
    .aigis-footer a:hover {
      text-decoration: underline; }

/*
---
name: ガイド・タイトル
tag:
  - parts
---

スタイルガイドのタイトルが入ります。

```html
<div class="aigis-title">StyleGuide Title</div>
```

こんな感じです。
*/
.aigis-title {
  font-size: 22px;
  font-weight: bold; }

/*
---
name: ページ内見出し
tag:
  - parts
---

```html
<div class="aigis-contents__title">StyleGuide Title</div>
<ul class="aigis-moduleList">
  <li class="aigis-moduleList__item">parts list</li>
  <li class="aigis-moduleList__item">parts list</li>
</ul>
```
*/
.aigis-contents__title {
  background-color: #8ac;
  color: #fff;
  font-size: 22px;
  font-weight: bold;
  padding: 1.5em; }

.aigis-moduleList {
  margin-bottom: 30px; }

/*
---
name: タグ
tag: parts
---

```html
<a class="aigis-tags__item" href="#">Tag Name</a>
```
*/
.aigis-tags {
  text-align: right;
  font-size: 0.95rem; }
  .aigis-tags__item {
    display: inline-block;
    color: #08c;
    text-decoration: none;
    border: 1px solid #dde;
    padding: 4px 6px;
    margin-left: 10px;
    border-radius: 3px; }
    .aigis-tags__item:before {
      font: normal normal normal 14px/1 FontAwesome;
      font-smoothing: antialiased;
      content: "\f02b";
      margin-right: 6px; }

a.aigis-tags__item:hover {
  color: #fff;
  background-color: #08c;
  text-decoration: none; }

/*
---
name: モジュール・タイトル
tag:
  - contents
---

モジュールのタイトルが入ります。

```html
<div class="aigis-module__heading">Module Title</div>
```

こんな感じです。
*/
.aigis-module__heading {
  font-size: 18px;
  font-weight: bold;
  border-left: 8px solid #8ac;
  margin: 100px 0 30px;
  padding: 5px 0 5px 15px; }

/*
---
name: プレビュー
tag: contents
---

```html
<div class="aigis-preview">プレビュー</div>
```
*/
.aigis-preview {
  border: 1px solid #dde;
  padding: 2em;
  margin: 2em 0; }

.aigis-codeblock .editor {
  border: 1px solid #dde;
  padding: 1em;
  margin-top: 2em;
  font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-size: 14px;
  line-height: 1.6; }

/*
---
name: カラーパレット
tag: contents
---

```html
<ul class="aigis-colorPalette">
  <li class="aigis-colorPalette__item" data-color="#888" style="background-color:#888;"></li>
  <li class="aigis-colorPalette__item" data-color="#f88" style="background-color:#f88;"></li>
  <li class="aigis-colorPalette__item" data-color="#8888ff" style="background-color:#88f;"></li>
</ul>
```
*/
.aigis-colorPalette {
  overflow: hidden;
  list-style: none;
  margin: 60px 0; }
  .aigis-colorPalette__item {
    float: left;
    width: 185px;
    height: 120px; }
    .aigis-colorPalette__item:after {
      display: inline-block;
      content: attr(data-color) "";
      font-size: 1.1rem;
      background-color: #fff;
      padding: 4px 8px;
      margin: 8px 0 0 8px;
      border-radius: 3px; }
